* {
  box-sizing: border-box; }

html,
body {
  height: 100%;
  font-family: 'Arial', Helvetica, sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 16px;
  color: #8494ab; }
  @media screen and (max-width: 768px) {
    html,
    body {
      font-size: 15px; } }
  @media screen and (max-width: 480px) {
    html,
    body {
      font-size: 14px; } }

body {
  line-height: 1.42;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

/*clearfix*/
.clearfix:before,
.clearfix:after {
  content: "";
  display: table; }

.clearfix:after {
  clear: both; }

.wrapper {
  display: table;
  width: 100%;
  height: 100%;
  background-color: #101114; }
  .wrapper__burger {
    margin: 20px; }

.body {
  max-width: 960px;
  margin: 80px auto; }
  @media screen and (max-width: 1000px) {
    .body {
      margin: 0 auto; } }

.aside {
  width: 320px;
  float: left;
  transition: .3s soft; }
  @media screen and (max-width: 1000px) {
    .aside {
      float: none;
      position: fixed;
      top: 0;
      left: -9999px; } }
  .aside__discard {
    margin: 20px 20px 0 90%; }
  .aside_open {
    left: 0;
    z-index: 15;
    background-color: #1f2229;
    height: 100%; }
    @media screen and (max-width: 430px) {
      .aside_open {
        width: 100%; } }

.main {
  max-width: 640px;
  float: right; }
  @media screen and (max-width: 1000px) {
    .main {
      float: none;
      margin: 0 auto; } }

.burger {
  display: none;
  width: 40px; }
  @media screen and (max-width: 1000px) {
    .burger {
      display: block; }
      .burger:hover .burger__line {
        background-color: #808592; } }
  .burger__line {
    display: block;
    height: 5px;
    width: 40px;
    background-color: #ffffff;
    border-radius: 3px;
    margin-bottom: 10px;
    transition: transform .1s linear, background-color .3s linear; }
    .burger__line:last-child {
      margin-bottom: 0; }

.discard {
  display: none;
  width: 40px; }
  @media screen and (max-width: 1000px) {
    .discard {
      display: block; } }
  .discard:hover .discard__line {
    background-color: #808592; }
  .discard__line {
    display: block;
    height: 5px;
    width: 40px;
    background-color: #ffffff;
    border-radius: 3px;
    margin-bottom: 10px; }
  .discard__line:nth-child(1n+0) {
    transform: rotate(45deg) translateY(20px); }
  .discard__line:nth-child(2n+0) {
    opacity: 0; }
  .discard__line:nth-child(3n+0) {
    transform: rotate(-45deg) translateY(-22px); }

.nav {
  width: 100%; }
  .nav__list {
    list-style: none;
    margin: 0;
    padding: 0; }
  .nav__item {
    display: block; }
    .nav__item_active .nav__link {
      color: #808592;
      background-color: #2a2d37; }
    .nav__item:hover .nav__link {
      color: #808592;
      background-color: #191c24; }
  .nav__link {
    display: block;
    padding: 30px 25px;
    font-family: 'Open-Sans', Helvetica, sans-serif;
    font-weight: bold;
    font-style: normal;
    font-size: 18px;
    color: #ffffff;
    text-decoration: none;
    background-color: #1f2229;
    transition: .3s linear; }
    @media screen and (max-width: 430px) {
      .nav__link {
        padding: 15px 10px; } }

.content {
  background-color: #f7f8f8;
  padding: 28px; }
  @media screen and (max-width: 430px) {
    .content {
      padding: 28px 5px; } }

.info-payment__title {
  font-family: 'Tele2-Russia-Text', Helvetica, sans-serif;
  font-weight: bold;
  font-style: normal;
  font-size: 22px;
  color: #383d43;
  margin: 15px 0 25px; }
  @media screen and (max-width: 670px) {
